@charset "UTF-8";
/* generic subpage */
@font-face {
  font-family: "Instrument Sans";
  font-display: swap;
  src: url("/_assets/fonts/Instrument_Sans/InstrumentSans-VariableFont_wdth-wght.woff2") format("woff2"), url("/_assets/fonts/Instrument_Sans/InstrumentSans.woff") format("woff"), url("/_assets/fonts/Instrument_Sans/InstrumentSans-VariableFont_wdth-wght.ttf") format("truetype");
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/* animations */
header,
main,
footer {
  opacity: 0;
  animation: fadeIn 0.5s;
  animation-fill-mode: forwards;
}

html {
  scrollbar-color: rgb(184, 140, 184) transparent;
  scrollbar-width: thin;
}

body {
  color: white;
  background-color: rgb(46, 41, 48);
  transition: background-color 0.25s ease-out;
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin: auto;
  margin-top: 8vh;
  width: 80%;
  max-width: fit-content;
}

header {
  position: fixed;
  width: min-content;
}

main {
  padding: 2em;
  padding-top: 1em;
  height: fit-content;
}

main,
main + footer {
  grid-column: 2;
  max-width: 500px;
}

body {
  font-family: "Instrument Sans", sans-serif;
  font-size: 1em;
}

header time {
  font-weight: 600;
}
header nav ul {
  border-bottom: 1px solid;
  border-top: 1px solid;
  border-color: purple;
  padding: 1em;
  padding-top: 1.5em;
}
header nav li {
  display: block;
  margin-bottom: 1em;
}
header nav li::before {
  content: "୨୧";
  margin-right: 0.5em;
}
header nav a {
  text-decoration: none;
  padding: 0px 5px;
  margin-left: -5px;
}

main {
  border: 1px solid;
  box-shadow: 5px 5px 10px rgba(128, 0, 128, 0.1);
  font-size: 1.1em;
  font-stretch: 98%;
  letter-spacing: 0.03em;
  line-height: 1.5em;
}

article ul ul {
  border-top: 1px solid;
  margin-top: 1em;
  padding-top: 1em;
}
article li:not(li:last-of-type) {
  margin-bottom: 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid;
}

h1 {
  text-align: left;
  font-size: 2em;
  letter-spacing: 1px;
}

h2 {
  padding-bottom: 10px;
  border-bottom: 1px solid hotpink;
  font-size: 1.8em;
  text-align: right;
}

h1,
h2,
h3,
h4 {
  font-weight: 600;
}

h3,
h4 {
  padding: 0.5em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

main + footer {
  margin: 5vh auto;
  text-align: center;
  font-size: 1em;
  width: 80%;
}
main + footer ul {
  padding: 0;
  list-style-type: none;
}
main + footer li {
  display: inline-block;
  margin-right: 1em;
}
main + footer li::before {
  content: "⬩";
  font-size: 20px;
  margin-right: 15px;
}
main + footer li:first-child::before {
  content: none;
}
main + footer li time {
  font-weight: 600;
}

a {
  color: plum;
  background-color: transparent;
  transition: all 0.2s ease-out;
  text-decoration-style: dashed;
  text-underline-offset: 3px;
  text-decoration-thickness: 1px;
}
a:hover {
  background-color: rgb(124, 49, 74);
  text-decoration: none;
  color: white;
}

button {
  padding: 0.5em;
  font-size: 1.2em;
  background-color: rgba(128, 0, 128, 0.267);
  border: 1px solid purple;
  border-radius: 5px;
  cursor: pointer;
}

::selection {
  background-color: rgb(255, 231, 179);
  color: black !important;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(164, 118, 170);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: plum;
}

@media all and (max-width: 768px) {
  body {
    display: flex;
    flex-direction: column;
    width: 90vw;
    margin-top: 5%;
  }
  header {
    margin: auto;
    padding-left: 5%;
    padding-right: 5%;
    width: 85%;
    position: static;
  }
  header nav ul {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    border: none;
  }
  header nav li {
    margin-right: 1em;
    margin-bottom: 0.5em;
    display: inline;
    flex-direction: row;
  }
  main {
    margin: auto;
    padding: 0 1em 1.5em;
    max-width: none;
  }
  main + footer {
    width: 90%;
  }
}